:root{--primary-default:#007FAB;--primary-dark:#003A4F;--primary-tint: #B0D2DE;--secondary-default: #FFC37D;--secondary-dark: #A46039;--secondary-tint: #FFE2A9;cursor:url("../img/cursor/cursor.svg"),auto}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1;font-family:"源柔ゴシックP-normal"}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;color:#000;outline:none;cursor:url("../img/cursor/cursor.svg"),auto}body{overflow-x:hidden;position:relative;background-color:#ffc37d;font-size:18px}h1{font-size:32px;font-family:"源柔ゴシックP-bold"}h5{font-size:24px;font-family:"源柔ゴシックP-bold"}section{position:relative}.container{width:1440px;position:relative;margin:0 auto}